.advantages {
  background-color: $color-ghost-white;
  margin-top: 0;

  @include vp-1199 {
    margin-top: -13px;

    @include vp-767 {
      margin-top: -35px;
    }
  }
}

.advantages__container {
  padding-top: 68px;
  padding-bottom: 113px;

  @include vp-1199 {
    padding-top: 59px;
    padding-bottom: 102px;
  }

  @include vp-767 {
    padding-top: 64px;
    padding-bottom: 70px;
    overflow-x: hidden;
  }
}

.advantages__title {
  margin-bottom: 45px;
  text-align: center;
  font-family: $ff-alumni-sans;
  color: $color-sapphire;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
    margin-bottom: 43px;
  }

  @include vp-767 {
    margin: 0;
    padding-top: 0;
    padding-bottom: 45px;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.advantages__list {
  padding: 0 0 0 79px;
  display: grid;
  grid-template-columns: 29.7% 22.2% 32.6%;
  column-gap: 5.45% ;
  row-gap: 40%;

  @include vp-1199 {
    grid-template-columns: 17.3% 27.6% 37.7% 19%;
    column-gap: 0;
    row-gap: 0;
    padding: 0;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.advantages__item--square {
  grid-column: 1/2;
  grid-row: 1/2;

  @include vp-1199 {
    grid-column: 1/3;
    grid-row: 1/2;
  }
}

.advantages__item--parking {
  grid-column: 3/4;
  grid-row: 1/2;

  @include vp-1199 {
    grid-column: 3/5;
    grid-row: 1/2;
  }
}

.advantages__item--equipment {
  grid-column: 2/3;
  grid-row: 1/3;

  @include vp-1199 {
    grid-column: 2/4;
    grid-row: 2/3;
  }
}

.advantages__item--convenience {
  grid-column: 1/2;
  grid-row: 2/3;

  @include vp-1199 {
    grid-column: 1/3;
    grid-row: 3/4;
  }
}

.advantages__item--experience {
  grid-column: 3/4;
  grid-row: 2/3;

  @include vp-1199 {
    grid-column: 3/5;
    grid-row: 3/4;
  }
}