@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/alumnisans-bold.woff2") format("woff2"), url("../fonts/alumnisans-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-medium.woff2") format("woff2"), url("../fonts/robotocondensed-medium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100hv;
}

html {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  font-size: 16px;
  background-color: #1e1e1e;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100hv;
  color: #000000;
  background-color: #ffffff;
  overflow-x: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1365px;
  margin: 0 auto;
  padding: 0 101px 0 103px;
}
@media (max-width: 1199.5px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767.5px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  background-color: #000000;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 30px;
  padding-bottom: 30px;
  min-height: 104px;
}
@media (max-width: 1199.5px) {
  .header__container {
    align-items: end;
    padding-top: 25px;
    padding-bottom: 25px;
    min-height: 82px;
  }
}
@media (max-width: 767.5px) {
  .header__container {
    align-items: start;
    min-height: 54px;
    padding-top: 15px;
    padding-bottom: 14px;
  }
}

@media (max-width: 1199.5px) {
  .header__logo {
    display: block;
    width: 81px;
    height: 17px;
  }
}
@media (max-width: 767.5px) {
  .header__logo {
    display: inline-block;
    width: 69px;
    height: 24px;
  }
}
.footer {
  display: flex;
  width: 100%;
  justify-content: end;
  background-color: #030a1a;
}
@media (max-width: 1199.5px) {
  .footer {
    justify-content: space-between;
  }
}
@media (max-width: 767.5px) {
  .footer {
    flex-direction: column;
  }
}

.footer__wrapper {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  column-gap: 25%;
}
@media (max-width: 1199.5px) {
  .footer__wrapper {
    justify-content: space-between;
    column-gap: 7.4%;
  }
}
@media (max-width: 767.5px) {
  .footer__wrapper {
    position: relative;
    width: 100%;
    display: block;
  }
}

.footer__logo {
  width: 20%;
}
@media (max-width: 1199.5px) {
  .footer__logo {
    width: 19.4%;
  }
}
@media (max-width: 767.5px) {
  .footer__logo {
    position: absolute;
    content: "";
    padding-top: 5px;
    width: 66px;
    height: 15px;
  }
}

.footer__nav {
  width: 55%;
}
@media (max-width: 1199.5px) {
  .footer__nav {
    width: 73%;
  }
}
@media (max-width: 767.5px) {
  .footer__nav {
    width: 100%;
  }
}

.footer__socials {
  width: 30%;
}
@media (max-width: 1199.5px) {
  .footer__socials {
    width: 21%;
  }
}
@media (max-width: 767.5px) {
  .footer__socials {
    width: 100%;
  }
}

.main {
  display: flex;
  flex-direction: column;
}

.phone {
  align-self: flex-start;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 26px;
  align-self: center;
  font-style: normal;
  line-height: 26px;
  font-weight: 600;
  letter-spacing: 0.78px;
  margin-bottom: 12px;
}
@media (max-width: 1199.5px) {
  .phone {
    display: block;
    min-height: 17px;
    font-size: 24px;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0.48px;
    align-self: flex-end;
    margin: 0;
  }
}
@media (max-width: 767.5px) {
  .phone {
    font-size: 20px;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.4px;
  }
}

@media (max-width: 767.5px) {
  .logo__img {
    object-fit: contain;
    height: 24px;
  }
}
.logo__img--footer {
  margin-top: 2px;
  width: 84px;
  height: 32px;
}
@media (max-width: 1199.5px) {
  .logo__img--footer {
    width: 81px;
    height: 18px;
    margin: 0;
    margin-top: 2px;
  }
}
@media (max-width: 767.5px) {
  .logo__img--footer {
    width: 66px;
    height: 15px;
    margin: 0;
  }
}

.hero {
  background-color: #000000;
  color: #ffffff;
}

.hero__container {
  position: relative;
  display: grid;
  grid-template-columns: 11.5% 22.8% 65.7%;
  min-height: 450px;
  margin-bottom: 51px;
}
@media (max-width: 1199.5px) {
  .hero__container {
    grid-template-columns: 48% 52%;
    min-height: 472px;
    margin-bottom: 0;
  }
}
@media (max-width: 767.5px) {
  .hero__container {
    grid-template-columns: 100%;
    min-height: 437px;
    margin-bottom: 0;
  }
}

.hero__main-information {
  grid-column: 2/3;
  grid-row: 1/2;
}
@media (max-width: 1199.5px) {
  .hero__main-information {
    grid-column: 1/2;
  }
}

.hero__city {
  padding: 62px 0 0 0;
  margin: 0 54px 20px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: 48px;
  letter-spacing: 1.44px;
  text-align: right;
  text-transform: lowercase;
}
@media (max-width: 1199.5px) {
  .hero__city {
    padding-top: 45px;
    margin: 0 6px 2px 0;
    font-size: 34px;
    letter-spacing: 1.02px;
  }
}
@media (max-width: 767.5px) {
  .hero__city {
    padding-top: 47px;
    margin: 0 4px 6px 0;
    font-size: 28px;
    letter-spacing: 0.84px;
  }
}

.hero__title {
  margin: 0 6px 48px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 92px;
  font-style: normal;
  font-weight: 600;
  line-height: 111px;
  letter-spacing: 4.6px;
  text-align: right;
  text-transform: uppercase;
  text-shadow: 5px 0 0 #ffffff;
}
@media (max-width: 1199.5px) {
  .hero__title {
    margin: 0 3px 37px 0;
    line-height: 86px;
    font-size: 77px;
    letter-spacing: 2px;
    text-transform: lowercase;
    text-shadow: 3px 0 0 #ffffff;
  }
  .hero__title::first-letter {
    text-transform: uppercase;
  }
}
@media (max-width: 1199.5px) and (max-width: 767.5px) {
  .hero__title {
    min-height: 137px;
    margin-bottom: 22px;
  }
}

.hero__link {
  display: inline-block;
  width: 237px;
  margin-left: 27px;
  margin-bottom: 27px;
  padding: 24px 56px 24px 56px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 5px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  border: 4px solid #ffffff;
}
@media (max-width: 1199.5px) {
  .hero__link {
    display: block;
    width: 188px;
    margin: 0;
    margin-left: auto;
    font-size: 11px;
    letter-spacing: 1px;
    padding: 24px 33px;
  }
}
@media (max-width: 767.5px) {
  .hero__link {
    width: 100%;
    text-align: center;
    font-size: 12px;
    letter-spacing: 0.4px;
    padding: 25px 84px 25px 84px;
  }
}

.hero__picture {
  grid-column: 3/4;
  grid-row: 1/2;
}
@media (max-width: 1199.5px) {
  .hero__picture {
    grid-column: 2/3;
    height: auto;
  }
}
@media (max-width: 767.5px) {
  .hero__picture {
    display: none;
  }
}

.hero__img {
  margin: 0;
  margin-top: -53px;
  padding-left: 62px;
  width: 73.5%;
  max-height: 556px;
}
@media (max-width: 1199.5px) {
  .hero__img {
    padding: 0;
    margin-top: 0;
    min-width: 324px;
    min-height: 390px;
    margin-left: auto;
    height: auto;
  }
}

.hero__advantages {
  grid-column: 1/2;
  grid-row: 1/2;
  display: flex;
  flex-direction: column;
  align-items: end;
  padding-top: 252px;
  margin-bottom: 25px;
}
@media (max-width: 1199.5px) {
  .hero__advantages {
    position: absolute;
    content: "";
    top: 0;
    left: 6%;
    grid-column: none;
    padding-top: 186px;
    padding-right: 30px;
  }
}
@media (max-width: 767.5px) {
  .hero__advantages {
    left: 4%;
    grid-column: none;
    padding-top: 178px;
    padding-right: 30px;
  }
}

.hero__advantage {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.nav__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 40px;
  align-items: center;
  margin-top: 1px;
}
@media (max-width: 1199.5px) {
  .nav__list {
    column-gap: 14px;
  }
}
@media (max-width: 767.5px) {
  .nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 16px 23px;
  }
}

.nav__item {
  display: inline-block;
}
@media (max-width: 1199.5px) {
  .nav__item {
    margin-left: 16px;
  }
}
@media (max-width: 767.5px) {
  .nav__item {
    margin: 0;
  }
}

.site-list {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}
@media (max-width: 767.5px) {
  .site-list {
    font-size: 16px;
  }
}

@media (max-width: 767.5px) {
  .site-list--servises {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
@media (max-width: 767.5px) {
  .site-list--abonements {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
@media (max-width: 767.5px) {
  .site-list--actions {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
@media (max-width: 767.5px) {
  .site-list--treners {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media (max-width: 767.5px) {
  .site-list--shedules {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
.socials {
  display: flex;
  justify-content: end;
}
@media (max-width: 767.5px) {
  .socials {
    justify-content: start;
  }
}

.socials__item {
  margin-right: 1px;
  margin-left: 11px;
}
@media (max-width: 1199.5px) {
  .socials__item {
    margin-left: 9px;
  }
}

.video {
  position: relative;
}
@media (max-width: 1199.5px) {
  .video {
    width: 39%;
    margin: 0 auto;
  }
}
@media (max-width: 767.5px) {
  .video {
    width: 320px;
    height: 167px;
    margin: 0 auto;
  }
}

.video__button {
  position: absolute;
  content: "";
}

.video__container {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background: rgba(20, 54, 122, 0.5);
  z-index: 0;
}
@media (max-width: 767.5px) {
  .video__container {
    width: 320px;
    height: 167px;
    left: -15px;
  }
}

.video__img {
  position: relative;
}
@media (max-width: 1199.5px) {
  .video__img {
    height: auto;
  }
}
@media (max-width: 767.5px) {
  .video__img {
    width: 320px;
    height: 167px;
  }
}

.video__container::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 54, 122, 0.5);
  z-index: 1;
}

@media (max-width: 1199.5px) {
  .video__iframe {
    max-width: 100%;
    width: 266px;
    height: 167px;
  }
}
@media (max-width: 767.5px) {
  .video__iframe {
    width: 100%;
    width: 320px;
    height: 167px;
    margin-left: -15px;
    margin-top: 3px;
  }
}

.gym__container {
  display: grid;
  grid-template-columns: 35.8% 1.9% 5.4% 26% 30.9%;
  grid-template-rows: 55.4% 33% 11%;
  min-height: 516px;
  padding-top: 120px;
  padding-bottom: 118px;
}
@media (max-width: 1199.5px) {
  .gym__container {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 39.5% 46.2% 12.8% 20%;
    min-height: 654px;
    padding-top: 102px;
    padding-bottom: 100px;
    margin-bottom: 85px;
  }
}
@media (max-width: 767.5px) {
  .gym__container {
    grid-template-rows: 27.5% 37.3% 19.6%;
    row-gap: 39px;
    padding-top: 65px;
    margin-bottom: 0;
    padding-bottom: 19px;
  }
}

.gym__wrapper {
  grid-column: 1/2;
  grid-row: 1/3;
  padding-left: 60px;
}
@media (max-width: 1199.5px) {
  .gym__wrapper {
    grid-column: 1/2;
    grid-row: 1/2;
    padding: 0;
  }
}
@media (max-width: 767.5px) {
  .gym__wrapper {
    grid-column: 1/2;
    grid-row: 1/2;
    padding: 0;
  }
}

.gym__image-wrapper {
  grid-column: 4/6;
  grid-row: 1/3;
}
@media (max-width: 1199.5px) {
  .gym__image-wrapper {
    grid-column: 1/2;
    grid-row: 2/4;
  }
}
@media (max-width: 767.5px) {
  .gym__image-wrapper {
    position: relative;
    grid-column: 1/2;
    grid-row: 2/3;
    min-width: 320px;
    min-height: 313px;
  }
}

.gym__video {
  grid-column: 3/5;
  grid-row: 2/4;
  height: auto;
}
@media (max-width: 1199.5px) {
  .gym__video {
    grid-column: 1/2;
    grid-row: 3/5;
  }
}
@media (max-width: 1199.5px) {
  .gym__video {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}

.about-gym {
  color: #102654;
}
@media (max-width: 1199.5px) {
  .about-gym {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }
}

.about-gym__title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.44px;
  margin: 40px 0 11px 0;
}
@media (max-width: 1199.5px) {
  .about-gym__title {
    display: block;
    min-width: 261px;
    margin: 0 0 2px 0;
    align-self: center;
    color: #102654;
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767.5px) {
  .about-gym__title {
    display: block;
    min-width: 290px;
    font-size: 32px;
    letter-spacing: 0.96px;
    text-align: left;
    margin-right: auto;
    margin-bottom: 7px;
  }
}

.about-gym__area {
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  margin: 0 0 40px 0;
}
@media (max-width: 1199.5px) {
  .about-gym__area {
    margin-bottom: 37px;
    letter-spacing: 1px;
    font-size: 16px;
  }
}
@media (max-width: 767.5px) {
  .about-gym__area {
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0px;
    min-width: 290px;
    margin-right: auto;
    margin-bottom: 24px;
  }
}

.about-gym__descripton {
  display: block;
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.2px;
  margin: 0;
}
@media (max-width: 1199.5px) {
  .about-gym__descripton {
    text-align: center;
    width: 74%;
    font-size: 16px;
    margin-bottom: 30px;
    letter-spacing: 0px;
  }
}
@media (max-width: 767.5px) {
  .about-gym__descripton {
    display: block;
    margin-right: auto;
    min-width: 258px;
    text-align: left;
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 27px;
  }
}

.about-gym__descripton1 {
  display: block;
  margin-bottom: 10px;
}

@media (max-width: 1199.5px) {
  .about-gym__descripton2 {
    display: none;
  }
}
.gym-image__img {
  margin: 0;
  width: 100%;
  height: auto;
}
@media (max-width: 767.5px) {
  .gym-image__img {
    position: absolute;
    content: "";
    object-fit: contain;
    object-position: center;
    min-height: 313px;
    top: 0;
    left: -15px;
    margin: 0 auto;
  }
}

.abonement {
  position: relative;
  background-color: #f8f9fc;
}
.abonement::after {
  position: absolute;
  content: "";
  background-image: url("../img/gym-equipment-desktop.png");
  background-repeat: no-repeat;
  background-position: -40px -30px;
  width: 321px;
  height: 384px;
  top: calc(100% - 313px);
  left: calc(100% - 246px);
  z-index: 1;
}
@media (max-width: 1199.5px) {
  .abonement::after {
    background-image: url("../img/gym-equipment-tablet.png");
    background-position: -40px -20px;
    top: calc(100% - 300px);
    left: calc(100% - 310px);
  }
}
@media (max-width: 767.5px) {
  .abonement::after {
    display: none;
  }
}

.abonement__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 122px;
  padding-bottom: 120px;
}
@media (max-width: 1199.5px) {
  .abonement__container {
    padding-top: 103px;
    padding-bottom: 102px;
  }
}
@media (max-width: 767.5px) {
  .abonement__container {
    padding-top: 64px;
    padding-bottom: 70px;
  }
}

.abonement__title {
